
import React, { useEffect, useState, useRef } from 'react';
import { formatTime } from '../util';

const PlaylistRow = ({ playlist, onClick, onDoubleClick, showBulb, bulbOn, onBulbClick }) => {
  return (
    playlist &&
    <div className='playlists-row' key={playlist.id} onClick={() => onClick && onClick(playlist.id)} onDoubleClick={() => onDoubleClick && onDoubleClick(playlist.id)}>
      <table style={{ width: "100%" }}>
        <tbody>
          <tr>
            <td style={{ width: 20 }}>
              <div className="playlists-image">
                <img style={{ width: 30 }} src={playlist.images && playlist.images[2] ? playlist.images[2].url : playlist.images && playlist.images[0].url} />
              </div>
            </td>
            <td>
              <table style={{ width: "100%", padding:4 }}>
                <tbody>
                  <tr>
                    <td>
                      <div className="playlists-name">
                        {playlist.name}
                      </div>
                    </td>
                    <td>

                    </td>
                  </tr>
                  <tr>
                    <td colSpan={2}>
                      <div className="playlists-count">
                        {playlist.count ? playlist.count : playlist.tracks.total} songs
                      </div>
                    </td>
                  </tr>
                </tbody>
              </table>
            </td>
            <td className="playlists-bulb-container">
              {true ?
                <div className={bulbOn ? "check" : "bulbOff"} onClick={(e) => { e.stopPropagation(); onBulbClick(playlist, bulbOn) }}></div> :
                null}
            </td>
          </tr>
        </tbody>
      </table>


    </div>
  )
}
export default PlaylistRow;